<template>
  <v-form v-model="valid">
    <v-text-field
      v-model="name"
      :rules="userNameRules"
      :label="$t('userName')"
      :counter="userNameLenMax"
      browser-autocomplete="nickname"
      required
      clearable
    ></v-text-field>
    <v-text-field
      v-model="email"
      :rules="emailRules"
      :label="$t('email')"
      browser-autocomplete="username"
      required
      clearable
    ></v-text-field>
    <v-text-field
      v-model="password"
      :rules="passwordRules"
      :label="$t('password')"
      type="password"
      browser-autocomplete="new-password"
      required
      clearable
    ></v-text-field>
    <div class="dflex justify-space-around wrap">
      <div>
        <v-btn :loading="state === 0" color="success" @click="register">
          {{ $t("register") }}
        </v-btn>
      </div>
      <div>
        <v-btn replace to="login" color="info">
          {{ $t("goToLogin") }}
          <v-icon right small>fa-arrow-right</v-icon>
        </v-btn>
      </div>
    </div>
  </v-form>
</template>

<script>
import { emailRules, passwordRules, userNameRules } from "../utils/form/rules";

import { register } from "../store/user";

import { USER_NAME_LEN_MAX } from "../constants";

export default {
  data: () => ({
    valid: true,

    email: "",
    password: "",
    name: "",
    avatar: "",

    userNameRules,
    userNameLenMax: USER_NAME_LEN_MAX,
    passwordRules,
    emailRules,

    state: 1
  }),

  methods: {
    async register() {
      if (!this.valid) {
        this.$alertInfo(this.$t("pleaseInputValid"));
        return;
      }

      this.state = 0;

      try {
        await register({
          email: this.email,
          password: this.password,
          name: this.name,
          avatar: this.avatar
        });

        this.state = 1;
      } catch (error) {
        this.$alertError(error);

        this.state = -1;
      }
    }
  }
};
</script>

<style lang="stylus" scoped>
@import '../styles/flex';
</style>
